'use client';

import { useEffect, useState } from 'react';

export default function WeddingCountdown() {
  const weddingDate = new Date('2025-07-12T14:30:00'); // ê²°í˜¼ì‹ ë‚ ì§œ
  const [timeLeft, setTimeLeft] = useState({
    days: 0,
    hours: 0,
    minutes: 0,
    seconds: 0,
  });

  useEffect(() => {
    const updateCountdown = () => {
      const now = new Date();
      const diff = weddingDate.getTime() - now.getTime();

      if (diff <= 0) return;

      const days = Math.floor(diff / (1000 * 60 * 60 * 24));
      const hours = Math.floor((diff / (1000 * 60 * 60)) % 24);
      const minutes = Math.floor((diff / (1000 * 60)) % 60);
      const seconds = Math.floor((diff / 1000) % 60);

      setTimeLeft({ days, hours, minutes, seconds });
    };

    updateCountdown(); // ì´ˆê¸° ì‹¤í–‰
    const interval = setInterval(updateCountdown, 1000); // 1ì´ˆë§ˆë‹¤ ê°±ì‹ 

    return () => clearInterval(interval);
  }, []);

  return (
    <section className='text-center text-gray-800 py-10 px-4 bg-[#fdfafa] rounded-md'>
      {/* ë‚ ì§œ í‘œê¸° */}
      <div className='mb-6'>
        <h2 className='text-xl font-bold'>2025.7.12</h2>
        <p className='text-sm text-gray-500 mt-1'>í† ìš”ì¼ ì˜¤í›„ 2ì‹œ 30ë¶„</p>
      </div>

      {/* ë‹¬ë ¥ (ê°„ë‹¨íˆ êµ¬ì„±ëœ í˜•íƒœ) */}
      <div className='text-sm leading-relaxed mb-6'>
        <div className='grid grid-cols-7 gap-1 text-gray-400 font-medium mb-2'>
          {['ì¼', 'ì›”', 'í™”', 'ìˆ˜', 'ëª©', 'ê¸ˆ', 'í† '].map((day) => (
            <div key={day}>{day}</div>
          ))}
        </div>
        <div className='grid grid-cols-7 gap-1 text-gray-700'>
          {[
            ...Array(2).fill(null), // 1ì¼ì€ í™”ìš”ì¼ì´ë¯€ë¡œ ì¼ìš”ì¼ ìë¦¬ëŠ” ë¹„ì›Œë‘ 
            ...Array.from({ length: 31 }, (_, i) => i + 1),
          ].map((day, idx) => {
            const isTarget = day === 12;
            return (
              <div
                key={idx}
                className={`w-8 h-8 flex items-center justify-center rounded-full ${
                  isTarget ? 'bg-gray-300 text-white font-semibold' : ''
                } ${day === null ? '' : ''}`}
              >
                {day || ''}
              </div>
            );
          })}
        </div>
      </div>

      {/* ë””ë°ì´ ì¹´ìš´íŠ¸ */}
      <div className='flex justify-center gap-3 mb-4'>
        <CountdownBox label='DAYS' value={timeLeft.days} />
        <CountdownBox label='HOUR' value={timeLeft.hours} />
        <CountdownBox label='MIN' value={timeLeft.minutes} />
        <CountdownBox label='SEC' value={timeLeft.seconds} />
      </div>

      <p className='text-sm text-gray-700'>
        ìƒí˜„ <span className='text-pink-500'>â™¥</span> ì§€í¬ì˜ ê²°í˜¼ì‹ì´{' '}
        <span className='text-pink-500 font-semibold'>{timeLeft.days}ì¼</span>{' '}
        ë‚¨ì•˜ìŠµë‹ˆë‹¤.
      </p>
    </section>
  );
}

// ë””ë°ì´ ë°•ìŠ¤ ì»´í¬ë„ŒíŠ¸
function CountdownBox({ label, value }: { label: string; value: number }) {
  return (
    <div className='bg-white border rounded-md px-3 py-2 shadow-sm'>
      <div className='text-lg font-bold'>{String(value).padStart(2, '0')}</div>
      <div className='text-xs text-gray-400 mt-1'>{label}</div>
    </div>
  );
}
